import React, { useState } from "react";
import FormManager from "../../../helper/FormManager";
import Navbar from "../../navbar/Navbar";
import usePost from "../../../services/usePost";

function Contact() {
  const [_message, set_message] = useState("");
  const [doPost] = usePost();

  const handleSubmit = (values: any) => {
    console.log("Register handleSubmit");
    console.log(values);

    doPost("contact", values, () => {
      set_message("Message submitted");
    });
  };
  return (
    <>
      <Navbar />

      {_message && <h2>{_message}</h2>}
      <FormManager initialValues={{ name: "", email: "", message: "" }}>
        {({ values, setValue }: any) => (
          <form
            onSubmit={(e) => {
              e.preventDefault();
              handleSubmit(values);
            }}
          >
            <input
              placeholder="name"
              value={values.name}
              type="text"
              maxLength={20}
              onChange={(e) => {
                setValue("name", e.target.value);
              }}
            />
            <input
              placeholder="Email"
              value={values.email}
              type="email"
              onChange={(e) => {
                setValue("email", e.target.value);
              }}
            />

            <input
              placeholder="message"
              value={values.message}
              type="text"
              onChange={(e) => {
                setValue("message", e.target.value);
              }}
            />
            <button type="submit">Register</button>
          </form>
        )}
      </FormManager>
    </>
  );
}

export default Contact;
